.add-input {
    width: 370px;
    height: 35px;
    padding: 16px 120px 16px 60px;
    font-size: 24px;
    border: none;
    border-radius: 20px;
    box-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.4);
    background: #ffffff;
}
@media (min-width: 0px) and (max-width: 988px) {
    .add-input {
        width: 350px;
        height: 45px;
        padding: 8px 95px 8px 30px;
        font-size: 16px;
    }
}
.add-input::-webkit-input-placeholder {
    color: #a6a6a6;
    font-style: italic;
}
.add-input::placeholder {
    color: #a6a6a6;
    font-style: italic;
}
.add-input:focus-visible {
    outline: none;
}
.add-input:focus {
    outline: none;
}
.todo-input-wrapper {
    position: relative;
}
.add-btn {
    width: 80px;
    height: 30px;
    border-radius: 30px;
    font-size: 14px;
    position: absolute;
    left: 450px;
    top: 18px;
    border: none;
    background: #3b7e7b;
    color: #ffffff;
    cursor: pointer;
}
@media (min-width: 0px) and (max-width: 988px) {
    .add-btn {
        width: 75px;
        height: 25px;
        left: 263px;
        top: 10px;
        font-size: 10px;
    }
}
    
.todo-item {
    display: flex;
    position: relative;
    width: 430px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #ffff00;
    padding: 12px 50px 12px 12px;
    border-radius: 30px;
    margin-top: 20px;
    opacity: 0.8;
    word-break: break-all;
}
.todo-item.checked {
    background-color:#00ff00;
}
@media (min-width: 0px) and (max-width: 988px) {
    .todo-item {
        width: 320px;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        margin-top: 15px;
        padding: 10px 80px 10px 20px;
    }
}
.todo-item-description {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.7;
    width: inherit;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-item-description {
        line-height: 1.4;
    }
}
.todo-item.deleting {
    background-color: #ff7f50;
    opacity: 0.9;
    -webkit-transform: translateX(250px) scale(0.8);
            transform: translateX(250px) scale(0.8);
    transition: 0.4s;
}
@media (min-width: 0px) and (max-width: 988px) {
    .todo-item.deleting {
        -webkit-transform: translateX(80px) scale(0.8);
                transform: translateX(80px) scale(0.8);
    }
}
.todo-item .tasks-btn-complete:hover {
    background: #32cd32;
}
.todo-item.checked {
    background-color: #32cd32;
    opacity: 0.8;
}
.todo-item.checked .tasks-btn-complete:hover {
    background: #ce2525;
}

.tasks-buttons {
    position: absolute;
    left: 380px;
    display: flex;
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-buttons {
        left: 240px;
    }
}
.tasks-btn-delete {
    width: 25px;
    height: 25px;
    background: rgba(0, 0, 0, 0);
    position: absolute;
    cursor: pointer;
    position: relative;
    padding-top: 10px;
    background-image: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}
.tasks-btn-delete::before {
    content: "";
    border-right: 25px solid #ce2525;
    width: 4px;
    height: 3px;
    position: absolute;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-btn-delete::before {
        border-right: 20px solid #ce2525;
        width: 20px;
    }
}
.tasks-btn-delete::after {
    content: "";
    border-right: 25px solid #ce2525;
    height: 6px;
    position: absolute;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-btn-delete::after {
        border-right: 20px solid #ce2525;
        width: 20px;
    }
}
.tasks-btn-delete:hover::after {
    transition: 0.5s;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.tasks-btn-delete:hover::before {
    transition: 0.5s;
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
}

.tasks-btn-complete {
    margin-right: 5px;
    height: 25px;
    width: 25px;
    border-radius: 50px;
    background-image: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
    background: #ffffff;
    padding: 5px;
    cursor: pointer;
    transition: 0.3s;
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-btn-complete {
        height: 20px;
        width: 20px;
        padding: 5px;
    }
}
.tasks-btn-complete:focus-visible {
    outline: none;
}
.tasks-btn-complete:checked .tasks-item {
    background-color: red;
}
.tasks-btn-complete:checked::before {
    content: "";
    border-top: 2px solid #00ff00;
    border-right: 2px solid #00ff00;
    width: 12.24px;
    height: 7.12px;
    position: absolute;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
@media (min-width: 0px) and (max-width: 988px) {
    .tasks-btn-complete:checked::before {
        width: 12.24px;
        height: 7.12px;
    }
}
.tasks-btn-complete:hover {
    transition: 0.5s;
}
.tasks-btn-complete:hover::before {
    content: "";
    border-top: 2px solid #ffffff;
    border-right: 2px solid #ffffff;
    width: 12.24px;
    height: 7.12px;
    position: absolute;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

